/* eter_spreadH */

/* spreadWindow */
.spreadWindow{
  width: 90vw;
  align-items:flex-start;
  overflow: scroll;
}
.grid_box{
  width: 60px;
  height: 90px;
  margin: 2px 4px;
}
.img_btn_reck{
  position: absolute;
  top: 60px;
  opacity: 0;
}
.img_btn_box:hover .img_btn_reck{
  width: 120%;
  display: flex;
  justify-content: space-between;
}
